import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import { Link, withRouter } from 'react-router-dom';

import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';

import * as Constant from '../constants';
import ImageMediaCard from './ImageMediaCard';
import { Grid } from '@material-ui/core';

const styles = theme => ({ 
  titleLink: {
    color: Constant.COLOR.primaryColor,
    textDecoration: 'none',
  },
  paper: {
    ...theme.mixins.gutters(),
    paddingTop: theme.spacing(2),
    paddingBottom: theme.spacing(2),
    marginBottom: theme.spacing(2),
  },
});

function OpenSourceListItem(props) {
  const { classes, item, history } = props;

  return (
    <Grid item xs={12} md={4} lg={4}>
      <ImageMediaCard
        image={item.image}
        imageHeight={180}
        title={item.name}
        body={item.description}
        tags={[]}
        onCardClick={() => history.push(`/articles/${item.id}`)}
      />
    </Grid>
  );

}

OpenSourceListItem.propTypes = {
  classes: PropTypes.object.isRequired,
  item: PropTypes.object.isRequired
};

const styledPage = withStyles(styles)(OpenSourceListItem);
export default withRouter(styledPage);