import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import { Link, withRouter } from 'react-router-dom';

import LazyLoad from 'react-lazyload';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import Chip from '@material-ui/core/Chip';

import ImageMediaCard from './ImageMediaCard';

import _ from 'lodash';

import * as Constant from '../constants';
import { Grid } from '@material-ui/core';

const styles = theme => ({ 
  titleLink: {
    color: Constant.COLOR.primaryColor,
    textDecoration: 'none',
  },
  paperInfo: {
    ...theme.mixins.gutters(),
    paddingTop: theme.spacing(2),
    paddingBottom: theme.spacing(2),
    marginBottom: theme.spacing(2),
  },
  chip: {
    margin: theme.spacing(1),
  },
});


function PortfolioListItem(props) {
  const { classes, item, history } = props;  

  return (
    <Grid item xs={12} md={4} lg={4}>
      <ImageMediaCard
        image={item.image}
        imageHeight={180}
        title={item.name}
        body={item.description}
        tags={item.tags}
        onCardClick={() => history.push(`/portfolio/${item.id}`)}
      />
    </Grid>
  );

}

PortfolioListItem.propTypes = {
  classes: PropTypes.object.isRequired,
  item: PropTypes.object.isRequired
};

const styledPage = withStyles(styles)(PortfolioListItem);
export default withRouter(styledPage);