import React from 'react';
import { connect } from 'react-redux';
import { getPortfolio } from '../actions';
import PropTypes from 'prop-types';

import { withStyles } from '@material-ui/core/styles';

import FlatList from '../components/FlatList';
import Spinner from '../components/Spinner';
import PortfolioListItem from '../components/PortfolioListItem';
import SideBar from '../components/SideBar';
import PageContent from '../components/PageContent';
import { Grid } from '@material-ui/core';

function mapStateToProps(state) {
  return state;
}

const styles = theme => ({
  root: {
    display: 'flex',
    flexDirection: 'row'
  },
  pageContent: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'space-around',
    overflow: 'hidden',
    backgroundColor: theme.palette.background.paper,
  }
});

class PortfolioList extends React.Component {

  constructor(props) {
    super();

    this.state = {
      loading: false,
      items: props.portfolio || []
    }
  }
  
  componentDidMount() {
    this.refresh();
  }

  refresh = () => {
    this.setState({ loading: true });
    this.props.getPortfolio();
  }

  componentWillReceiveProps(nextProps) {
    if (this.props.portfolio !== nextProps.portfolio) {
      this.setState({ items: nextProps.portfolio });
    }
    this.setState({ loading: false });
  }

  render() {
    const { loading, items } = this.state;
    const { classes } = this.props;

    return (
      <div className={classes.root}>
        <SideBar />
        <PageContent>
          <Grid 
            container 
            spacing={4} 
            direction="row">
            <FlatList 
              items={items}
              isLoading={loading}
              renderLoading={ <Spinner /> }
              renderEmpty={ () => <div>There are no items in the portfolio..</div>}
              renderItem={ item => <PortfolioListItem item={item} key={item.id} /> }
            />
          </Grid>
        </PageContent>
      </div>
    );
  }
}

PortfolioList.propTypes = {
  classes: PropTypes.object.isRequired,
};

const styledComponent = withStyles(styles)(PortfolioList);
export default connect(mapStateToProps, { getPortfolio })(styledComponent);