import React from 'react';
import { connect } from 'react-redux';
import { getPortfolioById, savePortfolioItem } from '../actions';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';

import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';

import TextField from '@material-ui/core/TextField';

import _ from 'lodash';
import ReactMarkdown from 'react-markdown';

import Spinner from '../components/Spinner';
import SideBar from '../components/SideBar';
import PageContent from '../components/PageContent';
import Breadcrumbs from '../components/Breadcrumbs';
import PaperFab from '../components/PaperFab';

function mapStateToProps(state) {
  return state;
}

const styles = theme => ({
  root: {
    display: 'flex',
    flexDirection: 'row'
  },
  paper: {
    ...theme.mixins.gutters(),
    paddingTop: theme.spacing(2),
    paddingBottom: theme.spacing(2),
    marginBottom: theme.spacing(2),
  },
  nameTextField: {
    fontSize: '2.16rem',
  },
  descriptionTextField: {
    fontSize: '1.27rem',
    fontWeight: 500,
  },
});

class PortfolioView extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      isLoading: false,
      isSaving: false,
      id: props.match.params.id || null,
      item: props.item || {},
      editMode: props.match.path === '/portfolio/:id/edit' ? true : false,
    }
  }
  
  componentDidMount() {
    this.refresh();
  }

  refresh = () => {
    const { portfolio } = this.props;
    const { id } = this.state;

    this.setState({ isLoading: true });

    if (_.isEmpty(portfolio)) {
      // Fetch live data
      this.props.getPortfolioById(id).then((doc => {
        let item = doc.data();
        this.setState({ item, isLoading: false});
      }))
    } else {
      // Portfolio Props found! Parsing into item...
      let item = _.find(portfolio, { id } )
      this.setState({ item, isLoading: false });
    }
  }

  handleChange = (value) => (event) => {
    this.setState(
      _.merge(this.state, { item: {[value]: event.target.value}})
    );
  };

  handleSave = () => {
    const { id, item } = this.state;
    this.setState({ isSaving: true });

    this.props.savePortfolioItem(id, item).then(result => {
      this.setState({ isSaving: false });
    })
  }

  render() {
    const { editMode, item, isLoading, isSaving } = this.state;
    const { classes } = this.props;

    if (_.isEmpty(item)) {
      return <Spinner />;
    } else {
      return (
        <div className={classes.root}>
          <SideBar />
          <PageContent>
            <Paper className={classes.paper} elevation={1}>
              <Breadcrumbs name={item.name} parentName="Portfolio" parentLink="/portfolio"/>

              { 
                editMode === false ?
                  // View
                  <div>
                    <Typography variant="h4" component="h4">
                      {item.name}
                    </Typography>
                    <Typography variant="h6" component="h6">
                      {item.description}
                    </Typography>
                    <ReactMarkdown source={item.body} />
                  </div>
                :
                  // Edit
                  <div>
                    <PaperFab 
                    icon={['fal', 'save']}
                    color={'white'}
                    vOffset={'-72'}
                    isLoading={isSaving}
                    onClick={() => this.handleSave()} />

                    <TextField
                      placeholder="Name"
                      inputProps={{className: classes.nameTextField}}
                      value={item.name}
                      onChange={this.handleChange('name')}
                      margin="normal"
                      fullWidth
                    />
                    <TextField
                      id="standard-name"
                      placeholder="Description"
                      inputProps={{className: classes.descriptionTextField}}
                      value={item.description}
                      onChange={this.handleChange('description')}
                      margin="normal"
                      fullWidth
                    />
                    <TextField
                      multiline
                      value={item.body}
                      onChange={this.handleChange('body')}
                      margin="normal"
                      fullWidth
                    />
                </div>
              }

            </Paper>
          </PageContent>
        </div>
      );
    }
  }

}

PortfolioView.propTypes = {
  classes: PropTypes.object.isRequired,
};

const styledComponent = withStyles(styles)(PortfolioView);
export default connect(mapStateToProps, { getPortfolioById,
                                          savePortfolioItem })(styledComponent);