import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import Button from '@material-ui/core/Button';
import { CircularProgress } from '@material-ui/core';

const styles = theme => ({ 
  button: {
    position: 'absolute',
    right: 40 + 'px',
  }
});

function PaperFab(props) {
  const { classes, isLoading, vOffset, label, icon, color, onClick } = props;

  return (
    <Button variant="fab" color="primary" aria-label={label} 
      className={classes.button} disabled={isLoading}
      style={{marginTop: vOffset + 'px'}}
      onClick={() => onClick()}>
        {
          !isLoading ?
          <FontAwesomeIcon icon={icon} size={'2x'} color={color} />
          :
          <CircularProgress color={'inherit'} size={24} style={{ color: color }} />
        }      
    </Button>
  )
}

PaperFab.propTypes = {
  classes: PropTypes.object.isRequired,
  isLoading: PropTypes.bool.isRequired,
  label: PropTypes.string,
  icon: PropTypes.array || PropTypes.string,
  color: PropTypes.string,
  vOffset: PropTypes.number,
  onClick: PropTypes.func
};

export default withStyles(styles)(PaperFab);
